import React from 'react';
import Link from 'next/link';

import PhoneIcon from './icons/PhoneIcon';
import SmMailIcon from './icons/SmMailIcon';
import UnderlinedLink from './ui/UnderlinedLink';

function ContactData(props) {
	return(
		<div className="contactData">
      <span className="footerTitle">Contact</span>

      <span className="iconLine">
        <SmMailIcon color="white" size={32} />
        <UnderlinedLink 
          href="mailto:info@williamvelazquez.com"
          text="info@williamvelazquez.com"
          color="#fff"
          hoverColor="#BF0404"
          lineColor="#BF0404"
        />
      </span>
      <span>
        {"Want to work together or any advice? "}
        <Link href="/contact">
          <a className="not-breakable">Let's talk!</a>
        </Link>
      </span>

      <style jsx>{`
        .contactData{
          display:grid;
          grid-template-columns: 1fr;
          grid-row-gap: 20px;
          justify-items: left;
        }
        .iconLine{
          display:grid;
          grid-template-columns: 30px 1fr;
        }
        p, span{
          color: #fff;
          text-align: center;
        }
        .not-breakable{
          white-space: nowrap;
        }
        .footerTitle{
          font-size: 20px;
          font-weight: bold;
          /* text-align: center; */
          /* border-top: 2px solid #fff; */
          /* border-bottom: 2px solid #fff; */
          /* padding: 5px; */
        }
        .footerTitle:after {
          content: " ";
          border: 1px solid #fff;
          /*margin-top: 10px;
          margin-bottom: 10px;*/
          margin: 10px 0px;
          display: block;
          max-width: 120px;/*80px*/
        }
        @media only screen and (max-width : 768px) {
          .contactData{
            justify-items: center;
          }
        }
      `}</style>
    </div>
	);
}

export default ContactData;